const mycurtain = () => {
  // body
  const bodyElement = document.querySelector('body');

  //body
  const curtainElement = document.createElement('div');
  curtainElement.setAttribute('class', 'leftCurtainbg');

  //loader
  const loader = document.createElement('div');
  loader.setAttribute('class', 'p-loader');

  //spinner
  const spinner = document.createElement('div');
  spinner.setAttribute('class', 'p-spinner');

  //bodyp-loader
  bodyElement.prepend(loader);
  //p-loaderp-spinner
  loader.prepend(spinner);
  // p-loader
  loader.after(curtainElement);

  //START: display:none; & Body"pageOn"()
  function js_secondary() {
    curtainElement.style.display = 'none';
    console.log('');
    console.log('none');
  }

  function js_primary() {
    document.body.classList.add('pageOn');
    console.log('');
    console.log('bodypageOn');
  }
  // setTimeout(() => {
  //   setTimeout(() => {
  //     //display:none
  //     curtainElement.style.display = 'none';
  //     console.log('');
  //     console.log('none');
  //   }, 1500);
  //   //bodyclass'pageOn'
  //   document.body.classList.add('pageOn');
  //   console.log('');
  //   console.log('bodypageOn');
  // });
  //END: display:none; & Body"pageOn"()

  /* */

  // & 
  //-- function
  function curtainFadeInOut() {
    setTimeout(() => {
      setTimeout(() => {
        bodyElement.style.display = 'none';
        console.log('bodystyle:none');
      }, 3000); //1000
      bodyElement.classList.add('fadeout');
      console.log('bodyfadeout');
    });
  }

  // function spinnerFadeOut() {
  //   //
  // }

  // 
  //beforeunloadunload
  //window.addEventListener('load', () => { //
  document.addEventListener('DOMContentLoaded', () => {
    let flg = null;
    let check_access = function () {
      // sessionStorage
      if (sessionStorage.getItem('access_flg')) {
        // 2
        flg = 1;
      } else {
        // 1
        sessionStorage.setItem('access_flg', true);
        flg = 0;
      }
      return flg;
    };
    let $i = check_access();
    if ($i == 0) {
      //1
      const loader = document.querySelector('.p-loader');
      //START: 
      setTimeout(() => {
        setTimeout(() => {
          // curtainFadeInOut();
          js_secondary();
          console.log('12');
        }, 2000);
        loader.classList.add('loaded');
        js_primary();
        console.log('11');
      }, 0);
    } else {
      // 2
      setTimeout(() => {
        setTimeout(() => {
          // curtainFadeInOut();
          js_secondary();
          console.log('22');
        }, 2000);
        loader.remove();
        js_primary();
        console.log('21');
      }, 0);
      // loader.remove();
      // console.log('21');
      // curtainFadeInOut;
      // console.log('22');
    }
  });
};

export default mycurtain;
